.luiza-heroes-home {
    &__header {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 90px;
        margin-top: 50px;
        text-align: center;
    
        img {
            max-width: 300px;
        }
    
        h1 {
            text-transform: uppercase;
            margin: 20px 0px 0px;
            color: #333;
            font-weight: 700;
            font-size: 1.7rem;
            line-height: 1.8rem;
        }
        
        p {
            font-size: 0.9rem;
            color: #666;
            margin: 10px 0px 50px;
        }
    
        
        @media (min-width:360px) {
           img {
            max-width: unset;
           }
    
           h1 {
            line-height: 2rem;
            font-size: 2rem;
           }
    
           p {
            font-size: 1.1rem;
           }
        }
    }   
    margin-bottom: 90px; 
}